import Vue from 'vue'
import Router from 'vue-router'
// 样例
// import HelloWorld from '@/components/HelloWorld'
//后期检查的时候搜索--后期删除---文字，然后删除该链接
// -----------------------用户控制台----------------------------

//用户控制台
const index = () => import('@/components/manage/index')
//用户控制台-添加设备
const addfacility = () => import('@/components/manage/addfacility')
//用户控制台-管理设备
const managefacility = () => import('@/components/manage/managefacility')
//用户控制台-管理设备-设备账台
const MFtaiZang = () => import('@/components/manage/MFtaiZang')
//用户控制台-管理设备-组态图
const MFconfiguration = () => import('@/components/manage/MFconfiguration')
//用户控制台-管理设备-组态图
const MFMechanics = () => import('@/components/manage/MFMechanics')
//用户控制台-管理设备-常用信息
const MFval = () => import('@/components/manage/MFval')
//用户控制台-项目管理
const MFprojectManagement = () => import('@/components/manage/projectManagement')
//用户控制台-项目管理-增加项目
const MFaddprojectManagement = () => import('@/components/manage/addprojectManagement')
//用户控制台-项目管理-查看项目和能对项目进行编辑
const MFseeProjectManagement = () => import('@/components/manage/seeProjectManagement')
//用户控制台-项目管理--关联员工
const MFsignproject= () => import('@/components/manage/signproject')
//用户控制台-权限管理
const powerfacility = () => import('@/components/manage/powerfacility')
//用户控制台-部门信息/部门管理
const userManagement = () => import('@/components/manage/userManagement')
//用户控制台-权限管理-项目权限
const PFrole = () => import('@/components/manage/PFrole')
//用户控制台-权限管理-系统权限
const PFsystem = () => import('@/components/manage/PFsystem')
//用户控制台-权限管理-项目权限-角色编辑个创建
const DFRdetails = () => import('@/components/manage/DFRdetails')
//用户控制台-权限管理-绑定设备
const binding = () => import('@/components/manage/binding')
//用户控制台-权限管理-系统权限-绑定用户
const associatedUser = () => import('@/components/manage/associatedUser')
// --------------------局部测试问题网页---后期删除------------------------
const Mdefault = () => import('@/components/manage/default')

// -----------------后台-------------------------

//后台-登陆
const bkLoginTwo= () => import('@/components/backstage/bkLogin')
//后台-主页
const bkIndex = () => import('@/components/backstage/index')
//后台-客户
const bkkeHu = () => import('@/components/backstage/keHu')
//后台-客户-查看用户设备
const bkmanagefacility = () => import('@/components/backstage/managefacility')
//后台-客户-查看用户设备--组态图
const bMFconfiguration = () => import('@/components/backstage/MFconfiguration')
//后台-客户-查看用户设备--机械数据
const bMFMechanics = () => import('@/components/backstage/MFMechanics')
//后台-查看设备维修记录列表
const bkequipmentList= () => import('@/components/backstage/equipmentList')
//后台-查看设备维修记录
const bkrepair= () => import('@/components/backstage/repair')
//后台-用户管理
const bkuserManagement= () => import('@/components/backstage/userManagement')
//后台-创建工单
const bkworkOrder= () => import('@/components/backstage/workOrder')
//后台-创建工单点个
const WOsingle= () => import('@/components/backstage/WOsingle')
//后台-客户历史投诉和解决方案
const bkcomplaintAndSolve= () => import('@/components/backstage/complaintAndSolve')
//后台-客户历史投诉和解决方案-客户投诉详情
const bkCASsingle= () => import('@/components/backstage/CASsingle')
//后台-更新知识库
const bkknowledge= () => import('@/components/backstage/knowledge')
//后台-更新知识库-问题描述
const bkKLDet= () => import('@/components/backstage/KLDet')
//后台-更新知识库-解决方案
const bkSolveDet= () => import('@/components/backstage/SolveDet')
//后台-更新知识库-问题描述修改
const selKLDet= () => import('@/components/backstage/selKLDet')
//后台-知识详情页面
const details= () => import('@/components/backstage/details')
//后台-更新行业资讯
const bknews= () => import('@/components/backstage/news')
//后台-更新行业资讯--新增页面
const newsDet= () => import('@/components/backstage/newsDet')
//后台-更新行业资讯--详情
const newsDetails= () => import('@/components/backstage/newsDetails')
//后台-更新企业动态
const dynamicB= () => import('@/components/backstage/dynamicB')
//后台-更新企业动态--新增
const dynamicBDet= () => import('@/components/backstage/dynamicBDet')
//后台-注册公司列表
const companylist= () => import('@/components/backstage/companylist')
//后台-关联用户页面
const signproject= () => import('@/components/backstage/signproject')
//用户控制台-项目管理--采集设备属性值
const applycollectdevice= () => import('@/components/backstage/applycollectdevice')
//后台-项目管理
const bkprojectManagement= () => import('@/components/backstage/projectManagement')
//后台-添加项目
const bkaddprojectManagement= () => import('@/components/backstage/addprojectManagement')
//后台-添加项目
const bkseeProjectManagement= () => import('@/components/backstage/seeProjectManagement')

// -----------------公放和注册登陆-------------------------

//登陆接口
const login = () => import('@/components/login/newLogin')
//用户信息详细补充
const detailed = () => import('@/components/login/detailed')
//忘记密码
const forget = () => import('@/components/login/forget')
//完善用户信息
const UserInformation = () => import('@/components/login/UserInformation')
//完善用户信息---更改密码
const UIChangepassword = () => import('@/components/login/Changepassword')
//完善用户信息---编辑和查询用户信息
const UItwo = () => import('@/components/login/UItwo')
//完善用户信息---创建公司
const createcompany = () => import('@/components/login/createcompany')
//完善用户信息---加入公司
const joincompany = () => import('@/components/login/joincompany')
//完善用户信息---我的公司
const myCompany = () => import('@/components/login/myCompany')
//上传用户头像测试单拉出来的页面---后期删除
const xxdetfult = () => import('@/components/login/xxdetfult')
//公放-主页
const homeIndex = () => import('@/components/home/index')
//公放-产品页
const product = () => import('@/components/home/product')
//公放-解决方案这一页
const solutions = () => import('@/components/home/solutions')
//公放-企业动态
const dynamic = () => import('@/components/home/dynamic')
//公放-企业动态每条相同的内容
const articleDetails = () => import('@/components/home/articleDetails')
//公放-行业资讯详情
const newsDetH = () => import('@/components/home/newsDetH')
//公放-关于我们
const aboutUs = () => import('@/components/home/aboutUs')
//公放-知识库
const knowledge = () => import('@/components/home/knowledge')
//公放-行业资讯
const news = () => import('@/components/home/news')
Vue.use(Router)


export default new Router({
  routes: [
    {//这是用户控制台
      path:"/index",
      component:index,
      children:[
        {
          path:'addfacility',
          component:addfacility,
        },
        {
          path:'managefacility',
          component:managefacility,
        },
        {
          path:'MFtaiZang',
          component:MFtaiZang,
        },
        {
          path:'MFconfiguration',
          component:MFconfiguration,
        },
        {
          path:'MFMechanics',
          component:MFMechanics,
        },
        {
          path:'MFval',
          component:MFval,
        },
        {
          path:'MFprojectManagement',
          component:MFprojectManagement,
        },
        {
          path:'MFaddprojectManagement',
          component:MFaddprojectManagement,
        },
        {
          path:'MFseeProjectManagement',
          component:MFseeProjectManagement,
          children:[
            {
              path:'MFsignproject',
              component:MFsignproject,
            }
          ]
        },
        {
          path:'powerfacility',
          component:powerfacility,
        },
        {
          path:'PFrole',
          component:PFrole,
        },
        {
          path:'PFsystem',
          component:PFsystem,
        },
        {
          path:'DFRdetails',
          component:DFRdetails,
        },
        {
          path:'binding',
          component:binding,
        },
        {
          path:'associatedUser',
          component:associatedUser,
        },
        {
          path:'userManagement',
          component:userManagement,
        },
        {
          path:'Mdefault',
          component:Mdefault,
        },
        {
          path:'',
          redirect:'/index/MFval'
        }
      ]
    },
    {
      path:"/bkLogin",
      component:bkLoginTwo,
    },
    {//这是后台的控制台
      path:"/bkIndex",
      component:bkIndex,
      children:[
        {
          path:"bkkeHu",
          component:bkkeHu,
        },
        {
          path:"bkmanagefacility",
          component:bkmanagefacility,
        },
        {
          path:"bMFconfiguration",
          component:bMFconfiguration,
        },
        {
          path:"bMFMechanics",
          component:bMFMechanics,
        },
        {
          path:"bkrepair",
          component:bkrepair,
        },
        {
          path:"bkuserManagement",
          component:bkuserManagement,
        },
        {
          path:"bkequipmentList",
          component:bkequipmentList,
        },
        {
          path:"bkworkOrder",
          component:bkworkOrder
        },
        {
          path:'WOsingle',
          component:WOsingle
        },
        {
          path:'bkcomplaintAndSolve',
          component:bkcomplaintAndSolve
        },
        {
          path:'bkCASsingle',
          component:bkCASsingle
        },
        {
          path:'bkknowledge',
          component:bkknowledge
        },
        {
          path:'selKLDet',
          component:selKLDet
        },
        {
          path:'bkKLDet',
          component:bkKLDet
        },
        {
          path:'bkSolveDet',
          component:bkSolveDet
        },
        {
          path:'bknews',
          component:bknews
        },
        {
          path:'newsDet',
          component:newsDet
        },
        {
          path:'newsDetails',
          component:newsDetails
        },
        {
          path:'dynamicB',
          component:dynamicB
        },
        {
          path:'dynamicBDet',
          component:dynamicBDet
        },
        {
          path:'companylist',
          component:companylist
        },
        {
          path:'details',
          component:details
        },
        {
          path:'bkprojectManagement',
          component:bkprojectManagement
        },
        {
          path:'bkaddprojectManagement',
          component:bkaddprojectManagement,
          children:[
            {
              path:'signproject',
              component:signproject
            },
            {
              path:'applycollectdevice',
              component:applycollectdevice
            },
          ]
        },
        {
          path:'bkseeProjectManagement',
          component:bkseeProjectManagement,
          children:[
            {
              path:'signproject',
              component:signproject
            },
            {
              path:'applycollectdevice',
              component:applycollectdevice
            },
          ]
        },
        {
          path:'',
          redirect:'/bkIndex/bkkeHu'
        }
      ]
    },
    {
      path:"/login",
      component:login,
    },
    {
      path:"/detailed",
      component:detailed,
    },
    {
      path:"/forget",
      component:forget,
    },
    {
      path:"/UserInformation",
      component:UserInformation,
      children:[
        {
          path:'UIChangepassword',
          component:UIChangepassword
        },
        {
          path:'UItwo',
          component:UItwo
        },
        {
          path:'createcompany',
          component:createcompany
        },
        {
          path:'joincompany',
          component:joincompany
        },
        {
          path:'myCompany',
          component:myCompany
        },
        {
          path:'',
          redirect:'/UserInformation/UItwo'
        }
      ]
    },
    {
      path:"/xxdetfult",
      component:xxdetfult,
    },
    {
      path:"/homeIndex",
      component:homeIndex,
    },
    {
      path:"/product",
      component:product,
    },
    {
      path:"/solutions",
      component:solutions,
    },
    {
      path:"/dynamic",
      component:dynamic,
    },
    {
      path:"/articleDetails",
      component:articleDetails,
    },
    {
      path:"/newsDetH",
      component:newsDetH,
    },
    {
      path:"/aboutUs",
      component:aboutUs,
    },
    {
      path:"/knowledge",
      component:knowledge,
    },
    {
      path:"/news",
      component:news,
    },
    {
      path:"*",
      redirect:"/homeIndex"
    }
  ]
})



