import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
    state:{
        user_id:'',
        userInfo:{},//用户信息存储
        static:'http://192.168.8.111:8000/static/upload/',//图片访问地址
        uploadimage:'http://192.168.8.111:8000/one/uploadimage/',//图片富文本编辑上传地址
        code_image_url:'http://192.168.8.111:8000/verification/image_codes/img_',//验证码地址
        tagList:[//规定的主题种类
            '汽轮机',
            '垃圾处理',
            '天然气',
            '压力容器',
            '计量器',
            '污水处理',
        ],
        buserInfo:{},//后台--用户信息存储
        companyname:'无',//公司名称
    },
    mutations:{
        setUser_id(state,change){
            state.user_id=change
        },
        setuserInfo(state,change){//获取用户信息
            state.userInfo=change
        },
        setcompanyname(state,change){//公司名称
            state.companyname=change
        },
        setBuserInfo(state,change){//获取后台用户信息
            state.buserInfo=change
        },
        DelMBuserInfo(state,change){//删除后台登陆信息
            state.buserInfo={}
        },
        DelMuserInfo(state,change){//删除前台登陆信息
            state.userInfo={}
        },
    },
    actions:{
        getUser_id(state,change){
            state.commit('setUser_id',change)
        },
        getuserInfo(state,change){//获取用户信息
            state.commit('setuserInfo',change)
        },
        getcompanyname(state,change){//公司名称
            state.commit('setcompanyname',change)
        },
        getBuserInfo(state,change){//获取后台用户信息
            state.commit('setBuserInfo',change)
        },
        DelABuserInfo(state,change){//删除后台登陆信息
            sessionStorage.removeItem('bkL');
            state.commit('DelMBuserInfo',change)
        },
        DelAuserInfo(state,change){//删除前台登陆信息
            sessionStorage.removeItem('logi');
            state.commit('DelMuserInfo',change)
        },
    },
    getters:{
        // jznum(state){
        //     return state.num
        // }
    }
})


